import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AboutUsComponent } from './about-us/about-us.component';
import { ContactUsComponent } from './contact-us/contact-us.component';
import { RefundAndCancellationPolicy} from './refund-and-cancellationPolicy/refund-and-cancellationPolicy.component';
import { ProductAndServicesComponent } from './product-and-services/product-and-services.component';
import { PrivacyAndPolicyComponent } from './privacy-and-policy/privacy-and-policy.component';
const routes : Routes = [
    {
        path : 'aboutUs',component : AboutUsComponent, 
    },
    {
        path : 'contactUs',component : ContactUsComponent, 
    },
    {
        path : 'privacyAndPolicy',component : PrivacyAndPolicyComponent, 
    },
    {
        path : 'refundAndCellationPolicy',component : RefundAndCancellationPolicy, 
    },
    {
        path : 'productAndServices',component : ProductAndServicesComponent, 
    },


]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class InformationRoutingModule{}